.style-switcher{
    position: fixed;
    top: 20px;
    right: 0;
    z-index: 10;
    background-color: #ffffff;
    padding: 15px;
    border: 1px solid #d4d4e3;
    border-radius: 0 0 0 4px;
    /* -webkit-transform: translateX(100%);
    transform: translateX(100%); */
    transition: all 0.3s ease;
}
.closeSwitcherSetting{
    transform: translateX(100%);
}
.openSwitcherSetting{
    transform: translateX(0%);
}
.style-switcher h5{
    font-size: 16px;
    color: #302e4d;
    margin-top: 0;
    margin-bottom: 8px;
    padding-bottom: 5px;
    border-bottom: 1px solid #e8dfec;
}
.style-switcher ul{
    margin: 0;
    padding: 0;
    list-style: none;
    display: block;
    margin-bottom: 5px;
}
.style-switcher ul li{
    display: inline-block;
}
.style-switcher ul li a{
   height: 30px;
   width: 30px;
   display: block;
   border-radius: 50%;
   cursor: pointer;
   text-decoration: none;
   margin-right: 5px;
}
.style-switcher .toggle-style-switcher {
    position: absolute;
    top: -1px;
    height: 45px;
    width: 45px;
    background-color: #ffffff;
    left: -46px;
    border: 1px solid #d4d4e3;
    border-right: none;
    text-align: center;
    cursor: pointer;
    border-radius: 4px 0 0 4px;
}
.style-switcher .toggle-style-switcher .fa{
    font-size: 21px;
    line-height: 43px;
}


