}
.checkboxTheme {
	/* opacity: 0; */
    position: absolute;
}

.labelTheme {
    /* background-color: #111; */
    background-color:  #877c9d;
	border-radius: 50px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 5px;
	position: relative;
	height: 26px;
	width: 50px;
	transform: scale(1.5);
}

.labelTheme .ball {
	background-color: #fff;
	border-radius: 50%;
	position: absolute;
	top: 2px;
	left: 2px;
	height: 22px;
	width: 22px;
	transform: translateX(0px);
	transition: transform 0.2s linear;
}

.checkboxTheme:checked + .labelTheme .ball {
	transform: translateX(24px);
}

