.section-portfolio {
  background-color: #f2f2fc;
  margin: 60px 0px;
  padding: 0 0 70px ;
  min-height: 100vh;
  display: block;
  left: 270px;
  top: 0;
  right: 0;
  z-index: 0;
  opacity: 1;
  transition: all 0.3s ease;
  width: 100%;
  /* overflow: hidden; */
}
.content-portfolio{
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
.list-item-portfolio{
  display: flex;
  width: 100%;
  padding: 0;
  margin: 0;
  justify-content: center;
  align-items: center;
}
.portfolio .portfolio-filter {
  /* display: flex; */
  /* -ms-flex: 0 0 100%; */
  flex: 0 0 100%;
  max-width: 100%;
  text-align: center;
  margin-bottom: 40px;
  padding-left: 0px;
  justify-content: center;
  align-items: center;
}
.portfolio .portfolio-filter button {
  border: none;
  background-color: transparent;
  font-size: 18px;
  font-weight: 700;
  text-transform: capitalize;
  margin: 0 10px;
  color: #302e4d;
  cursor: pointer;
  border-bottom: 2px solid #ec1839;
  white-space: nowrap;
  padding-bottom: 2px;
  transition: all 0.3s ease;
}
.portfolio .portfolio-item {
  /* background-color: red; */
